.object-container {
  background: #bdbdbd;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 5px 5px 5px -2px rgba(0, 0, 0, 0.48);
}

.object-container div {
  /* width: 100%; */
  height: 100%;
  overflow: hidden;
  /* border-radius: 10px; */
  position: relative;
  cursor: pointer;
}

.object-container img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.object-container span {
  position: absolute;
  color: white;
  background-color: #ae967ced;
  height: 17%;
  width: 100%;
  left: 0;
  bottom: 0;
  padding: 15px;
  font-weight: 600;
  font-family: "Arial";
  box-sizing: border-box;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.object-container a {
  right: 0;
  position: absolute;
  bottom: 5px;
  right: 10px;
  font-size: smaller;
  text-decoration: none;
  color: #333e5b;
}
